import React, {useState, useContext} from 'react';
import {SubmitButton} from './SubmitButton';
import {useAuth} from '../hooks/useAuth';
import {StoreContext} from '../contexts/StoreContext';
const defaut_optionValues = [false,false,false,]
const optionList = ["option1", "option2", "option3", ];

function ListItem({id, label, onClick, isCheckedArr = defaut_optionValues, extraInfo= null}){
    return(
            <li id={id} htmlFor={"checkbox-"+id} className="cursor-pointer flex py-2 w-full border-b items-center justify-between rounded hover:bg-gray-100 dark:hover:bg-gray-600" onClick = {onClick} >
                <div className= "md-text-lg w-full">
                    <input id={"checkbox-"+id} type="checkbox" value="" className="h-4 mr-2" checked={isCheckedArr[id]}/>
                    <label className="ml-2 w-full">
                        {label}
                    </label>         
                </div> 
                <div className="text-red-500">
                    {extraInfo}
                </div>
            </li>
    );
}

export default function TextSelect(_) {
    const [optionValues, setOptionValues] = useState(defaut_optionValues);
    const store = useContext(StoreContext);
    const {token} = useAuth();

    const handleClick = (event) => {
        event.preventDefault();
        const id = parseInt(event.currentTarget.id);
        const newOptionValues = [...optionValues];
        newOptionValues[id] = !newOptionValues[id];
        setOptionValues(newOptionValues);
    }

    const handleSubmit = (event) => {

    }


    if(store.selected_text.text===null) 
        return <></>
    return(
        <form onClick = {handleSubmit}>
            <div className='mb-2'> Select text cleaning options:</div>
            <div id="cleaningOptions" className="z-10 bg-white rounded-lg shadow bg-slate-300 dark:bg-slate-900">
                <ul className="h-48  px-3 pb-3 overflow-y-auto">
                    {optionList.map((option, i) => 
                        <ListItem key={i} id = {i} label = {option} isCheckedArr = {optionValues} onClick = {handleClick}
                        />
                    )}
                </ul>
                <SubmitButton label = "Clean Text"/>
            </div>
        </form>
    );
}